<template>
   <section id="assets">
      <span class="title">{{$t('property.user')}}</span>
      <el-col :span="24" style="padding-bottom: 0px" class="toolbar">
        <el-form :inline="true" :model="userForm" ref="userForm">
            <el-form-item prop="enterpriseName">
                <el-input :placeholder="$t('pleaseEnter')" v-model="input" class="input-with-select">
                  <el-select v-model="select" slot="prepend" :placeholder="$t('Pchoose')">
                     <el-option label="UID" value="uid"></el-option>
                     <!-- <el-option :label="$t('phone')" value="phoneNumber"></el-option>
                     <el-option :label="$t('userName')" value="username"></el-option>
                     <el-option :label="$t('email')" value="email"></el-option>  -->
                  </el-select> 
                </el-input>
            </el-form-item>
             <el-form-item>
                 <el-button class="warning" v-on:click="getUsers">{{$t('search')}}</el-button>
            </el-form-item>
            <el-form-item>
                 <el-button class="info" @click="handleReset('userForm')">{{$t('reset')}}</el-button>                 
            </el-form-item>
        </el-form>
      </el-col>

      <el-table :data="userList"   v-loading="userLoading" style="width: 100%;" :row-class-name="tableRowClassName" >
        <el-table-column prop="uid"  :label="$t('property.tableHead[0]')">
        </el-table-column>
        <el-table-column prop="totalAssets" :label="$t('property.tableHead[1]')">
        </el-table-column>
        <el-table-column prop="usdgNum"  :label="$t('property.tableHead[2]')">
        </el-table-column>
        <el-table-column prop="btyNum"  :label="$t('property.tableHead[3]')">
        </el-table-column>
      </el-table>
      <div class="page" v-show="total>10">
      <el-col :span="24" class="toolbar  ">
         <el-pagination
          background
          layout="prev, pager, next"
          :total="total" @current-change="handleCurrentChange">
        </el-pagination>
      </el-col>
      </div>
   </section>
</template>
   
<script>
import {ajax,dateStr2GmtTimeStamp,getDataYear} from '../../common/js/common.js'
export default {
   data(){
     return{
      total:0,
      input:"",
       select:"",
       userForm:{
         startTime:'',
         endTime:'',
         enterpriseName:''
       },
       userLoading:false,
       userList:[],
       acountDate:{
          page:1,
          rowNum:"10",
       },
       total:0,
     }
   },
   methods:{
    // 查询用户资产信息
    queryFrontUserAccount:function(){
       let  data = {
          page:this.acountDate.page,
          rowNum:this.acountDate.rowNum,
          uid:this.input,
          // phoneNumber:this.input,
          // username:this.input,
          // email:this.input,
         }
        ajax(this,this.ApiConfig.assetManagement.queryFrontUserAccount,data,(res) => {
            console.log(res)


            this.userList = res.data.rows;
            var arr = this.userList;
            arr.forEach((item,i,arr)=>{
              arr[i].totalAssets=arr[i].totalAssets.toFixed(2)
            })

            this.total =res.data.total;
        },"POST")
    },
     getUsers:function(){
        this.queryFrontUserAccount();
     },
     handleReset:function(formName){
        this.$refs[formName].resetFields();
        this.select="";
        this.input = "";
     },
     tableRowClassName:function({row, rowIndex}){
         if(rowIndex % 2 == 1){
            return "success-row"
         }
     },
      handleCurrentChange:function(val){
       this.acountDate.page = val;
       this.queryFrontUserAccount();
     },
   },
   mounted:function(){
    this.queryFrontUserAccount();
   }
}
</script>

<style>
#assets{
  .el-select .el-input {
    width: 105px;
  }
}
</style>




